<template>
  <div>
    <div class="register-container">
      <div class="register">
        <div class="form" v-if="!googleAuthPage">
          <div class="error-box" v-if="errorStatus">
            {{ errorText }}
          </div>
          <div class="input-box">
            <span>Your Email</span>
            <input type="text" required v-model="userData.email" />
          </div>
          <div class="input-box">
            <span>Your Password</span>
            <input type="password" required v-model="userData.password" />
          </div>
          <div class="button">
            <button @click="login()">Login</button>
          </div>
        </div>
        <div class="form qr-code" v-else>
          <div class="img-box" v-if="googleAuthQrCodeImageView">
            <img :src="googleAuthQrCodeImage" alt="" />
          </div>
          <div class="error-box" v-if="errorStatus">
            {{ errorText }}
          </div>
          <div class="input-box">
            <span>Enter the Google authentication code.</span>
            <input
              type="text"
              required
              v-model="googleAuthUserToken"
              @keypress.enter="googleAuthEncoding()"
              maxlength="6"
              @keypress="isNumber($event)"
            />
          </div>
          <div class="button">
            <button @click="googleAuthEncoding">Send</button>
          </div>
        </div>
      </div>
    </div>
    <div class="sign-info">
          <span class="dark-color d-inline-block line-height-2">
            Don't have an account?
            <router-link to="/auth/sign-up1" class="iq-waves-effect pr-4">
              Sign up
            </router-link>
          </span>
    </div>
  </div>
</template>

<script>
import speakeasy from 'speakeasy'
import qrcode from 'qrcode'
import cryptoJS from 'crypto-js'
import api from '@/router/api'
export default {
  name: 'Register',
  data () {
    return {
      userData: {
        email: '',
        password: ''
      },
      googleAuthPage: false,
      errorStatus: false,
      errorText: 'Please fill in all the blanks!',
      googleAuthQrCodeImage: '',
      googleAuthQrCodeImageView: true,
      googleAuthUserToken: '',
      secret: ''
    }
  },
  methods: {
    login () {
      if (this.userData.email.length > 2 && this.userData.password.length > 2) {
        const reg = /^(([^<>()\]\\.,;:\s@"]+(\.[^<>()\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/
        if (reg.test(this.userData.email)) {
          api
            .post('/users/login', {
              email: this.userData.email,
              password: this.userData.password
            })
            .then(res => {
              if (res.data.status === 1) {
                this.googleAuthQrCodeImage = res.data.image
                this.secret = res.data.secret
                this.googleAuthPage = true
                this.googleAuthQrCodeImageView = false
                this.$store.dispatch('Setting/authUserAction', {
                  auth: true,
                  authType: 'email',
                  user: {
                    id: res.data.user.id,
                    identificationNumber: res.data.user.identificationNumber,
                    fullName: res.data.user.fullName,
                    phone: res.data.user.phone,
                    email: res.data.user.email,
                    password: res.data.user.password,
                    token: res.data.user.user_token,
                    country: res.data.user.country,
                    city: res.data.user.city
                  }
                })
              } else if (res.data.status === 2) {
                this.googleCreatedAuth()
                this.googleAuthPage = true
                this.googleAuthQrCodeImageView = true
                this.$store.dispatch('Setting/authUserAction', {
                  auth: true,
                  authType: 'email',
                  user: {
                    id: res.data.user.id,
                    fullName: res.data.user.fullName,
                    phone: res.data.user.phone,
                    email: res.data.user.email,
                    password: res.data.user.password,
                    token: res.data.user.user_token
                  }
                })
              } else if (res.data.status === 0) {
                this.errorStatus = true
                this.errorText = 'Wrong information, check and correct!'
                setTimeout(() => {
                  this.errorStatus = false
                }, 5000)
              } else {
                this.errorStatus = true
                this.errorText = 'An error has occurred, please try again!'
                setTimeout(() => {
                  this.errorStatus = false
                }, 5000)
              }
            })
            .catch(() => {
              this.errorStatus = true
              this.errorText = 'An error has occurred, please try again or contact us!'
              setTimeout(() => {
                this.errorStatus = false
              }, 5000)
            })
        } else {
          this.errorText = 'Enter a valid email address.'
          this.errorStatus = true
          setTimeout(() => {
            this.errorStatus = false
          }, 5000)
        }
      } else {
        this.errorText = 'Please fill in all the blanks!'
        this.errorStatus = true
        setTimeout(() => {
          this.errorStatus = false
        }, 5000)
      }
    },
    googleCreatedAuth () {
      var secret = speakeasy.generateSecret({
        name: 'reflexmarkets'
      })
      qrcode.toDataURL(secret.otpauth_url, (err, data) => {
        if (err) throw err
        this.googleAuthQrCodeImage = data
      })
      this.secret = secret.ascii
    },
    googleAuthEncoding () {
      const verified = speakeasy.totp.verify({
        secret: this.secret,
        encoding: 'ascii',
        token: this.googleAuthUserToken
      })
      if (verified === true) {
        let token = this.userData.email + this.userData.password
        token = cryptoJS.AES.encrypt(
          this.token,
          this.token + 'reflex'
        ).toString()
        api
          .post('/users/login/setToken', {
            token: token,
            email: this.userData.email,
            password: this.userData.password
          })
          .then(res => {
            if (res.data.status === 1) {
              api
                .post('/users/login/setAuth', {
                  email: this.userData.email,
                  password: this.userData.password,
                  qr_code_image: this.googleAuthQrCodeImage,
                  qr_code_secret: this.secret
                })
                .then(res => {
                  if (res.data.status === 1) {
                    localStorage.setItem('reflex', token)
                    this.$router.push('/')
                  } else if (res.data.status === 2) {
                    localStorage.setItem('reflex', token)
                    this.$router.push('/')
                  } else {
                    this.errorStatus = true
                    this.errorText = 'Wrong code, please check and re-enter!'
                    setTimeout(() => {
                      this.errorStatus = false
                    }, 5000)
                  }
                })
            }
          })
      } else {
        this.errorStatus = true
        this.errorText = 'Something went wrong. Please try again!'
        setTimeout(() => {
          this.errorStatus = false
        }, 5000)
      }
    },
    isNumber: function (evt) {
      evt = evt || window.event
      const charCode = evt.which ? evt.which : evt.keyCode
      if (
        charCode > 31 &&
        (charCode < 48 || charCode > 57) &&
        charCode !== 46
      ) {
        evt.preventDefault()
      } else {
        return true
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.register-container {
  width: 100%;
  display: flex;
  .register {
    height: 100%;
    flex: 1;
    display: grid;
    place-content: center;
    .form {
      .input-box {
        margin: 20px 0;
        span {
          display: block;
          margin-bottom: 3px;
          font-size: 17px;
        }
        input {
          outline: none;
          background: none;
          width: 400px;
          height: 40px;
          border: 1px solid rgba(#ccc, 0.5);
          border-left: 4px solid #cccccc;
          font-size: 17px;
          border-radius: 2px;
          padding-left: 5px;
          transition: 300ms;
          &:focus,
          &:valid {
            border-left-color: #ff7130;
          }
        }
      }
      .button {
        text-align: right;
        button {
          cursor: pointer;
          outline: none;
          border: 2px solid transparent;
          background: #ff7130;
          padding: 10px 30px;
          color: #ffffff;
          font-size: 20px;
          font-weight: 500;
          font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
          Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
          border-radius: 2px;
          transition: 300ms;
          &:hover {
            border-color: #ff7130;
            color: #ff7130;
            background: transparent;
          }
        }
      }
    }
    .form.qr-code {
      .img-box {
        margin: 0 auto;
        width: 200px;
        height: 200px;
      }
      .input-box input {
        margin-top: 5px;
      }
    }
  }
  .image {
    height: 100%;
    flex: 1;
    background: #ff7130;
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
      width: 100%;
      height: 100%;
    }
  }
}
.resendCodeWarning {
  position: fixed;
  width: 100%;
  padding: 10px 0;
  background: black;
  color: white;
  bottom: 0;
  text-align: center;
}
@media (max-width: 1320px) {
  .register-container {
    height: max-content !important;
    flex-direction: column-reverse;
    .image {
      overflow: hidden;
      height: 350px;
      padding: 15px 0;
      svg {
        height: 100%;
        width: 100%;
      }
    }
    .register {
      padding: 15px 0 50px 0;
    }
  }
}
@media (max-width: 450px) {
  .register-container {
    .image {
      margin: 20px 0;
    }
    .register {
      .form {
        width: 100vw;
        padding: 15px 25px 0 25px;
        .input-box input {
          width: 100% !important;
        }
        .button button {
          padding: 5px 15px;
        }
      }
    }
  }
}
</style>
